@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  height: 100%;
  min-height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
  min-height: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #d6cac3;
  height: 100%;
  min-height: 100%;
  margin: 0;
}

.wrapper {
  position: relative;
  min-height: 100vh;
}

.container {
  padding-bottom: 140px;
}
.header-container {
    display: flex;
    height: 100px;
    background-color: #1f1f1f;
    color: #a6a6a6;
    align-items: center;
    justify-content: space-between;
}

.header-button-container {
    display: flex;
    background-color: inherit;
    align-items: center;
    margin-right: 4rem;
}

div#title {
    margin-left: 3rem;
    font-family: 'Permanent Marker', cursive;
    font-size: 32px;
}

.button {
    background-color: #333333 !important;
    color: #a6a6a6 !important;
    margin-right: 1rem !important;
}

.button:hover {
    background-color: #4d4d4d !important;
}

#login-button {
    padding: 0;
    margin-left: 3rem;
}
.modal-container {
    height: 320px;
    width: 450px;
    
}

input[type=text], input[type=password], input[type=email] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
    border-radius: 20px;
}

.label-input {
    margin-top: 50px;
}

.button {
    background-color: #333333 !important;
    color: #a6a6a6 !important;
}

.button:hover {
    background-color: #4d4d4d !important;
}

.footer-container {
    position: absolute;
    bottom: 0;
    height: 140px;
    width: 100%;
}

.footer-button-container {
    background-color: #333333;
    height: 98px;
    width: 100%;
    text-align: center;    
}

.footer-button-container>div {
    display: inline-block;
    position: relative;
    bottom: -25px;
    padding: 0 20px;
}


.copyright {
    position: absolute;
    background-color: #1f1f1f;
    height: 42px;
    width: 100%;
}

.copyright>p {
    text-align: center;
    color: rgba(255, 255, 255, 0.664);
    font-style: italic;
    font-size: 14px;
}



.card-container {
    width: 200px;
    margin: 20px 30px;
    display: inline-block;
}

.tutor-name {
    margin: 0;
    text-align: left;
}

.tutor-description {
    text-align: left;
}


.main-container>p{
    font-size: 100px;
    text-align: center;
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
    width: 90%;
    padding: 2%;
    font-family: 'Permanent Marker', cursive;
}

input[type=text], input[type=email], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: none;
  }

  textarea {
      height: 150px;
  }

  
  input[type=submit] {
    background-color: #a19994b2;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  input[type=submit]:hover {
    background-color: #b8aea8a2;
  }

  .contact-form {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    width: 600px;
    height: 500px;
    margin: 100px auto;
  }
.join-container>p{
    font-size: 80px;
    text-align: center;
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
    width: 90%;
    padding: 2%;
    font-family: 'Permanent Marker', cursive;
}
